* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.login-form {
    width: 100%;
    height: 100vh;
    background-color: rgb(255, 255, 255);
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("https://images.unsplash.com/photo-1473448912268-2022ce9509d8?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1141&q=80");
    /* background-image: url("https://images.unsplash.com/photo-1448375240586-882707db888b?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80"); */
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
    flex-direction: column;
    gap: 20px;
}

.login-form form {
    background-color: rgba(0, 0, 0, 0.219);
    backdrop-filter: blur(5px);
    min-height: 25rem;
    width: 22rem;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    padding: 10px;
}

.login-form form label{
    color: rgb(0, 232, 212);
    margin-bottom: 5px;
}


.login-form form input{
    width: 18rem;
    height: 2.6rem;
    border: none;
    border-radius: 5px;
    padding: 0 10px;
}

.login-form form input:focus{
    background-color: rgb(235, 255, 235);
    outline: none;

}


.login-form h3{
    color: rgb(255, 255, 255);
    text-shadow: 5px 5px 15px black;
}

.login-form form button{
    width: 10rem;
    height: 2.6rem;
    border: none;
    background-color: rgb(0, 232, 212);
    font-weight: 500;
    color: white;
    border-radius: 5px;
    margin-top: 15px;
}


.login-form p{
    color: rgb(255, 255, 255);
}

.login-form button{
    background-color: white;
    border: 2px solid rgb(107, 107, 107);
    text-decoration: none;
    padding: 5px 15px;
    color: rgb(0, 242, 255);
    border-radius: 5px;
    font-weight: 400;
}

@media (max-width:430px) {
    .login-form form{
        width: 18.5rem;
    }
    .login-form form input{
        width: 15rem;
    }
}
